html,
body {
  overflow: hidden;
  margin: 0;
  padding: 0;
  /*Avoid flicker on slides transitions for mobile phones #336 */
  -webkit-tap-highlight-color: transparent;
}

.pp-section {
  height: 100%;
  position: absolute;
  width: 100%;
}

.pp-easing {
  -webkit-transition: all 1000ms cubic-bezier(0.55, 0.085, 0, 0.99);
  transition: all 1000ms cubic-bezier(0.55, 0.085, 0, 0.99);
  /* custom */
  -webkit-transition-timing-function: cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-timing-function: cubic-bezier(0.55, 0.085, 0, 0.99);
  /* custom */
}

#pp-nav {
  position: fixed;
  z-index: 100;
  margin-top: -32px;
  top: 50%;
  opacity: 1;
}

#pp-nav.right {
  right: 17px;
}

#pp-nav.left {
  left: 17px;
}

.pp-section.pp-table {
  display: table;
}

.pp-tableCell {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
}

.pp-slidesNav {
  position: absolute;
  z-index: 4;
  left: 50%;
  opacity: 1;
}

.pp-slidesNav.bottom {
  bottom: 17px;
}

.pp-slidesNav.top {
  top: 17px;
}

#pp-nav ul,
.pp-slidesNav ul {
  margin: 0;
  padding: 0;
}

#pp-nav li,
.pp-slidesNav li {
  display: block;
  width: 16px;
  height: 16px;
  margin: 12px 7px;
  position: relative;
}

.pp-slidesNav li {
  display: inline-block;
}

#pp-nav li a,
.pp-slidesNav li a {
  display: block;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  cursor: pointer;
  text-decoration: none;
}

#pp-nav li .active span,
.pp-slidesNav .active span {
  background: #ffffff;
}

#pp-nav span,
.pp-slidesNav span {
  width: 100%;
  height: 100%;
  background: #d5d2cd;
  border-radius: 50%;
  position: absolute;
  z-index: 1;
  border: 1px solid #d5d2cd !important;
}

.pp-tooltip {
  position: absolute;
  top: -2px;
  color: #fff;
  font-size: 14px;
  font-family: arial, helvetica, sans-serif;
  white-space: nowrap;
  max-width: 220px;
}

.pp-tooltip.right {
  right: 20px;
}

.pp-tooltip.left {
  left: 20px;
}

.pp-scrollable {
  overflow-y: scroll;
  height: 100%;
}

.site-header {
  position: fixed;
  z-index: 10;
}

.first,
.second,
.third {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.first {
  background-image: url(../img/first.jpg);
}

.second {
  background-image: url(../img/second.jpg);
}

.third {
  background-image: url(../img/third.jpg);
}

.menu-wrapper {
  position: fixed;
  right: 0;
  top: 0;
}

.toggle-menu {
  position: fixed;
  z-index: 11;
  right: 0;
  top: 0;
}

.menu__inner {
  top: 0;
  right: 0;
  height: 100%;
  position: fixed;
  width: 400px;
  background: rgba(0, 0, 0, 0.5);
  -webkit-transform: translateX(400px);
  -ms-transform: translateX(400px);
  transform: translateX(400px);
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.menu__inner.visible {
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
}

.menu__inner .menu {
  margin: 0;
  padding: 200px 85px 0 40px;
  list-style-type: none;
  text-align: right;
}

.menu__inner .menu li {
  margin-bottom: 30px;
}

.menu__inner .menu a {
  font: 20px "freshmannormal";
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}

.menu__inner .menu li.current-menu-item a,
.menu__inner .menu a:hover {
  color: #d47521;
}